// service supplier action
import {Action} from '@ngrx/store';

export const TRY_FETCH_SERVICESUPPLIER = 'TRY_FETCH_SERVICESUPPLIER';
export const SET_SERVICESUPPLIER = 'SET_SERVICESUPPLIER';
export const SERVICESUPPLIER_EDIT = 'SERVICESUPPLIER_EDIT';
export const UPLOAD_SERVICESUPPLIER_IMAGES = 'UPLOAD_SERVICESUPPLIER_IMAGES';

// classes for service supplier
export class TryFetchServiceSupplier implements Action {
  readonly type = TRY_FETCH_SERVICESUPPLIER;
}

export class SetServiceSupplier implements Action {
  readonly type = SET_SERVICESUPPLIER;

  constructor(public payload: {}) {
  }
}

export class UploadServiceSupplierImages {
  readonly type = UPLOAD_SERVICESUPPLIER_IMAGES;

  constructor(public payload: any) {
  }
}

export class ServiceSupplierEdit implements Action {
  readonly type = SERVICESUPPLIER_EDIT;

  constructor(public payload: {}) {
  }
}

export type ServicesupplierActions = ServiceSupplierEdit | UploadServiceSupplierImages | SetServiceSupplier | TryFetchServiceSupplier;
