import {Injectable} from '@angular/core';
import {Actions, Effect, ofType} from '@ngrx/effects';
import {map, switchMap} from 'rxjs/operators';
import {HttpClient, HttpResponse} from '@angular/common/http';
import * as ServiceSupplierActions from './servicesupplier.actions';
import * as endPoints from '../../../../shared/serverEndpoints';

@Injectable()
export class ServicesupplierEffects {
  constructor(public action: Actions, public http: HttpClient) {
  }

  // effects for service supplier
  @Effect()
  editServiceSupplier = this.action.pipe(
    ofType(ServiceSupplierActions.SERVICESUPPLIER_EDIT),
    map((payload: ServiceSupplierActions.ServiceSupplierEdit) => {
      return payload.payload;
    }),
    switchMap((data) => {
      return this.http.post(endPoints.editservicesuppliers, data, {observe: 'response'});
    }),
    switchMap((response: HttpResponse<any>) => {
      if (response.status === 200) {
        return [
          {
            type: ServiceSupplierActions.SET_SERVICESUPPLIER,
            payload: response.body
          }
        ];
      }
    })
  );


  @Effect()
  uploadServiceSupplierImages = this.action.pipe(
    ofType(ServiceSupplierActions.UPLOAD_SERVICESUPPLIER_IMAGES),
    map((data: ServiceSupplierActions.UploadServiceSupplierImages) => {
      return data.payload;
    }), switchMap((data: { registrationCinImage: File, registrationGstImage: File, registrationMsmeImage: File, registrationRcImage: File }) => {
      return this.http.post(endPoints.uploadservicesuppliersImages, data, {observe: 'response'});
    }),
    switchMap((response: HttpResponse<any>) => {
      if (response.status === 200) {
        return [
          {
            type: ServiceSupplierActions.SET_SERVICESUPPLIER,
            payload: response.body
          }
        ];
      }
    })
  );

  @Effect()
  fetchServiceSupplier = this.action.pipe(
    ofType(ServiceSupplierActions.TRY_FETCH_SERVICESUPPLIER),
    switchMap(() => {
      return this.http.get(endPoints.fetchservicesuppliers, {observe: 'response'});
    }),
    switchMap((response: HttpResponse<any>) => {
      if (response.status === 200) {
        return [
          {
            type: ServiceSupplierActions.SET_SERVICESUPPLIER,
            payload: response.body
          }
        ];
      }
    })
  );

}
