
import {ServicesupplierModel} from '../servicesupplier.model';
import {ServicesupplierActions} from './servicesupplier.actions';
import {AppState} from "../../../../store/app.reducer";

export interface ServicesupplierState extends AppState {
  serviceSupplier: State;
}

export interface State {
  serviceSupplier: ServicesupplierModel;
}

const initialState: State = {
  serviceSupplier: new ServicesupplierModel('', '', '', '', '', '', '', '', '', '', {
    permanentAddress: {
      address: '',
      pincode: '',
      phoneNo: ''
    }
  }, {
    profileRegistrar: '', name: '', mobile: '', email: '', pan: '', address: {
      address: '',
      phoneNo: '',
      pincode: ''
    }, bankDetails: {
      bankAccountNo: '',
      bankAddress: '', bankIfsc: '', bankName: ''
    }
  }, '', '', '')
};

export function ServiceSupplierReducer(state = initialState, action: ServicesupplierActions) {
  switch (action.type) {
    case 'SET_SERVICESUPPLIER':
      return {
        ...state,
        serviceSupplier: action.payload
      };
    default: {
      return {
        ...state
      };
    }
  }
}
