import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {ServicesupplierComponent} from './servicesupplier.component';
import {ServicesuppliereditComponent} from './servicesupplieredit/servicesupplieredit.component';


const routes: Routes = [
  {
    path: '', component: ServicesupplierComponent, children: [
      {path: 'dashboard', component: ServicesupplierComponent},
      {path: 'editProfile', component: ServicesuppliereditComponent}
    ]
  },

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ServicesupplierRoutingModule { }
